"use client"
import { Card } from '@/components/ui/Card';
import { Activity, Users, Calendar, Clock, AlertCircle, CheckCircle } from 'lucide-react';
import RecentActivities from './RecentActivities';
import UpcomingEvents from './UpcomingEvents';
import { Metadata } from 'next';
import {
    Users,
    Calendar,
    Settings,
    LayoutDashboard,
    LogOut,
    ChevronDown,
    Bell,
    Search,
    School,
    CalendarCheck
} from 'lucide-react';
const metadata: Metadata = {
    title: 'Admin Dashboard',
    description: 'Overview of system statistics and activities',
};

export default function DashboardPage() {
    // بيانات إحصائية وهمية - يمكن استبدالها ببيانات حقيقية من API
    const stats = [
        {
            title: "Total Events",
            value: "42",
            icon: <Calendar className="w-6 h-6 text-indigo-600" />,
            change: "+12%",
            trend: 'up'
        },
        {
            title: "Active Users",
            value: "1,284",
            icon: <Users className="w-6 h-6 text-blue-600" />,
            change: "+5%",
            trend: 'up'
        },
        {
            title: "Pending Approvals",
            value: "8",
            icon: <AlertCircle className="w-6 h-6 text-amber-600" />,
            change: "-2",
            trend: 'down'
        },
        {
            title: "Completed Events",
            value: "36",
            icon: <CheckCircle className="w-6 h-6 text-green-600" />,
            change: "+18%",
            trend: 'up'
        }
    ];

    return (
        <div className="flex-1 flex flex-col overflow-hidden">
            <header className="bg-white shadow-sm z-10">
                <div className="flex items-center justify-between p-4">
                    <div className="flex items-center">
                        <button
                            onClick={() => setSidebarOpen(!sidebarOpen)}
                            className="p-2 rounded-lg hover:bg-gray-100 mr-2"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
                            </svg>
                        </button>

                        <div className="relative ml-4">
                            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                            <input
                                type="text"
                                placeholder="Search..."
                                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                            />
                        </div>
                    </div>

                    <div className="flex items-center space-x-4">
                        <div className="relative">
                            <button className="p-2 rounded-full hover:bg-gray-100 relative">
                                <Bell className="text-gray-600 w-5 h-5" />
                                {notifications.some(n => !n.read) && (
                                    <span className="absolute top-0 right-0 w-2 h-2 bg-red-500 rounded-full"></span>
                                )}
                            </button>
                        </div>

                        <div className="flex items-center space-x-2">
                            <div className="w-8 h-8 bg-indigo-100 rounded-full flex items-center justify-center">
                                <span className="text-indigo-800 font-medium">A</span>
                            </div>
                            <div>
                                <p className="text-sm font-medium">Admin</p>
                                <p className="text-xs text-gray-500">Super Admin</p>
                            </div>
                            <ChevronDown className="text-gray-500 w-5 h-5" />
                        </div>
                    </div>
                </div>
            </header>
            <div className="space-y-6">
            {/* إحصائيات سريعة */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                {stats.map((stat, index) => (
                    <Card key={index} className="p-6 hover:shadow-md transition-shadow">
                        <div className="flex justify-between items-start">
                            <div>
                                <p className="text-sm font-medium text-gray-500">{stat.title}</p>
                                <p className="text-2xl font-bold mt-1">{stat.value}</p>
                            </div>
                            <div className="p-2 rounded-lg bg-gray-100">
                                {stat.icon}
                            </div>
                        </div>
                        <div className={`mt-3 text-sm font-medium ${stat.trend === 'up' ? 'text-green-600' : 'text-red-600'
                            }`}>
                            {stat.change} {stat.trend === 'up' ? '↑' : '↓'}
                        </div>
                    </Card>
                ))}
            </div>

            {/* قسمين رئيسيين */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* النشاطات الأخيرة */}
                <Card className="p-6">
                    <div className="flex items-center justify-between mb-4">
                        <h2 className="text-lg font-semibold">Recent Activities</h2>
                        <button className="text-sm text-indigo-600 hover:underline">
                            View All
                        </button>
                    </div>
                    <RecentActivities />
                </Card>

                {/* الأحداث القادمة */}
                <Card className="p-6">
                    <div className="flex items-center justify-between mb-4">
                        <h2 className="text-lg font-semibold">Upcoming Events</h2>
                        <button className="text-sm text-indigo-600 hover:underline">
                            View All
                        </button>
                    </div>
                    <UpcomingEvents />
                </Card>
            </div>

            {/* قسم إضافي */}
            <Card className="p-6">
                <h2 className="text-lg font-semibold mb-4">Quick Actions</h2>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                    <button className="flex flex-col items-center justify-center p-4 border rounded-lg hover:bg-gray-50 transition-colors">
                        <div className="p-3 bg-indigo-100 rounded-full mb-2">
                            <Activity className="w-5 h-5 text-indigo-600" />
                        </div>
                        <span className="text-sm font-medium">Create Event</span>
                    </button>
                    {/* أزرار إضافية... */}
                </div>
            </Card>
        </div >
        </div>

    );
}